.button {
    display: inline-block;
    align-items: center;
    vertical-align: middle;
    padding: 0;

    height: 44px;

    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.3;

    color: var(--accent-color);
    background-color: var(--main-bg-color);
    border: 1px solid transparent;
    border-radius: 22px;

    cursor: pointer;

    transition: background-color var(--timing-transition);


    &:hover,
    &:focus {
        background-color: var(--main-btn-hover-bg-color);
    }
}


.button__icon-send {
    display: inline-block;
    align-items: center;
    vertical-align: middle;
    margin-left: 12px;
}

// header (btn"Buy now" - class="button button--header")

.button--header {
    width: 209px;
    
    @include respond-to('medium') {
        width: 186px;
    }

    @include respond-to('large') {
        width: 159px;
    }

}

// button--hero (btn"How it’s made?" - class="button button--hero"  btn"How it’s made?" - class="button button--hero button--pink")

.button--hero {
    font-weight: 700;
    font-size: 16px;

    width: 83px;
    height: 24px;

    @include respond-to('large') {
        width: 138px;
        height: 40px;
    }
}

.button--pink {
    height: 24px;
    width: 62px;

    @include respond-to('large') {
        width: 104px;
    }

    color:var(--main-bg-color);
    background-color:var(--accent-color);

    transition: var(--timing-transition);

    &:hover,
    &:focus {
        background-color: var(--accent-color);
    }
}

// button--readMore (btn"Read more" - class="button button--about"

.button--about {
    width: 176px;
}

// button--contacts (btn"Franchise" - class="button button--white"  btn"Our Locations" - class="button button--colored")

.button--white {
    width: 161px;

    @include respond-to('medium') {
        width: 174px;
    }

    @include respond-to('large') {
        width: 168px;
    }
}

.button--colored {
    width: 161px;

    @include respond-to('medium') {
        width: 192px;
    }

    @include respond-to('large') {
        width: 198px;
    }

    color: var(--main-bg-color);
    background-color:var(--accent-color);

    transition: var(--timing-transition);

    &:hover,
    &:focus {
        background-color:var(--accent-color);
    }
}